import Select from "../common/select.vue"
export default{
	components:{
		Select
	},
	data(){
		return{
			val1:1,		
			id:null,
			form:{},//安全报告详情
			action:{}
		}
	},
	onLoad(e){
		this.id=e.id
		this.getList()
	},
	methods:{
		getList(){
			// 司机、车况报告
			this.$http.post("operatorReport/detailBoardReport",{
				lineRecordId:this.id
			}).then(res=>{
				if(res.code==100){
					this.form=res.info	
				}
			})
			// 司机行为监督详情
			this.$http.post("operatorReport/detailOperationReport",{
				lineRecordId:this.id
			}).then(res=>{
				if(res.code==100){
					this.action=res.info	
				}
			})
		},
	}
}
